#headerImage {
  background: #8bdeff url("logo.f3cc24b5.png") center space;
  height: calc(66vh - 56px);
  background-size: 192px;
}
#heroTextContainer {
  background-color: rgba(255, 255, 255, 0.588);
  animation: heroTextContainer-text-shadow-move 1.5s ease-out;
  backdrop-filter: blur(4px);
  border-radius: 100px 100px 10px;
  transition: 1s ease-out;
}
#heroText {
  text-shadow: 5px 5px #8bdeff;
  font-size: 7vw;
  animation: heroText-text-shadow-move 1.5s ease-out, heroText-text-move 1.5s ease-out;
  transition: 1s ease-out;
}
#heroText:hover,
#heroText:focus {
  text-shadow: 10px 20px #8bdeff;
  color: rgba(0, 0, 0, 0);
}
#heroTextContainer:hover,
#heroTextContainer:focus {
  border-radius: 100px 100px 100px 10px;
}
@keyframes heroText-text-move {
  0% {
    transform: translate(-160px, -120px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}
@keyframes heroText-text-shadow-move {
  0% {
    text-shadow: 160px 120px #8bdeff;
    color: rgba(0, 0, 0, 0);
  }
  75% {
    color: rgba(0, 0, 0, 0);
  }
  100% {
    text-shadow: 5px 5px #8bdeff;
    color: black;
  }
}
@keyframes heroTextContainer-text-shadow-move {
  0% {
    transform: translate(160px, 120px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}
